


























export default {
	data() {
		return {
			list: []
		}
	},
	onLoad() {
		this.loadData();
	},
	methods: {
		async loadData() {
			this.list = await this.$api.json('lazyLoadList');
		},
		imageOnLoad(index) {
			this.$set(this.list[index], 'loaded', 'loaded');
			
			//测试用加载完成时间
			let date = new Date();
			let minute = date.getMinutes();
			let second = date.getSeconds();
			minute = minute < 10 ? '0'+minute : minute;
			second = second < 10 ? '0'+second : second;
			let time = `${minute}:${second}`;
			this.$set(this.list[index], 'loadedTime', time);
		},
		imageOnError(index) {
			console.log(index)
			this.list[index].src = '/static/errorImage.jpg';
		}
	},

}
